import React from 'react';

const HelpModal = () => {
  return (
    <div id='help'>
      <div className='descBox'>
        <div style={{ flex: '1 1 0' }}></div>
        <p className='helpDesc'></p>
      </div>
      <input type='checkbox' id='modal'/>
      <label htmlFor='modal' className='fas fa-question-circle modalbutton' />
      <div className='modalLayer'>
        <div className='modalWindow'>
          <div className='modalTop'>
            <div className='modalTitle'></div>
            <label htmlFor='modal' className='fas fa-times modalclose'></label>
          </div>
          <div className='modalBody'>
            <p className='motelText'>
              <i className='fas fa-chevron-circle-right'></i>
                 8     .
            </p>
            <p className='motelText'>
              <i className='fas fa-chevron-circle-right'></i>
               <span> </span><span>d/D</span><span> </span>          .   .    1 .
            </p>
            <p className='motelText'>
              <i className='fas fa-chevron-circle-right'></i>
              <span> </span><span>d/D</span><span> </span>  <span>+</span>      .     .
            </p>
            <p className='motelText'>
              <i className='fas fa-chevron-circle-right'></i>
                    . <span> </span><span>d/D</span><span> </span>            .
            </p>
          </div>
        </div>
        <label htmlFor='modal' className='modalBack'></label>
      </div>
    </div>
  );
};

export default HelpModal;